import React from "react";
import { connect } from "react-redux";
import { setTimer } from "../../myRedux";

import "./Timer.css";

class Timer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      time: 0,
      start: 0,
      isOn: false,
      action: this.props.action,
    };

    this.startTimer = this.startTimer.bind(this);
    this.stopTimer = this.stopTimer.bind(this);
  }

  componentDidUpdate() {
    if (this.props.action === "start" && this.state.isOn === false)
      this.startTimer();

    if (this.props.action === "stop" && this.state.isOn === true)
      this.stopTimer();

    if (this.props.action === "reset" && this.state.isOn === false) {
      this.resetTimer();
      this.props.setTimer("stop");
    }
  }

  resetTimer() {
    if (this.state.time > 0) this.setState({ time: 0 });
  }

  startTimer() {
    this.setState({
      time: this.state.time,
      start: Date.now(),
      isOn: true,
    });

    this.timer = setInterval(
      () =>
        this.setState({
          time: Date.now() - this.state.start,
        }),
      10
    );
    console.log(this.timer);
  }

  stopTimer() {
    if (this.state.isOn === true) {
      clearInterval(this.timer);
      this.setState({ isOn: false });
    }
  }

  render() {
    let min =
      Math.floor(Math.floor(this.state.time / 1000) / 60) < 10
        ? "0" + Math.floor(Math.floor(this.state.time / 1000) / 60)
        : Math.floor(Math.floor(this.state.time / 1000) / 60);
    let sec =
      Math.floor(Math.floor(this.state.time / 1000) % 60) < 10
        ? "0" + Math.floor(Math.floor(this.state.time / 1000) % 60)
        : Math.floor(Math.floor(this.state.time / 1000) % 60);
    let milesec =
      Math.floor(Math.floor(this.state.time % 1000) / 10) < 10
        ? "0" + Math.floor(Math.floor(this.state.time % 1000) / 10)
        : Math.floor(Math.floor(this.state.time % 1000) / 10);

    return (
      <div className="main-timer">
        <h1 className="black">Timer</h1>

        <span className="timer">{min + " : " + sec + " : " + milesec}</span>
        <p className="black">
          This timer is for the animations and not for the implement of
          algorithms
        </p>

        <hr></hr>

        <h6 className="developed">Developed by</h6>
        <h3>Ali Khutaba</h3>

        <div className="container">
          <div style={{ display: "inline-block" }}>
            <div className="social-title">Linkedin :</div>
            <div className="cube">
              <a
                href="https://www.linkedin.com/in/ali-khutaba-843627173/"
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  className="linkedin-img"
                  src="data:image/png;base64,"
                  alt="Click her"
                ></img>
              </a>
            </div>
          </div>

          <div style={{ display: "inline-block" }}>
            <div className="social-title">GitHub :</div>
            <div className="cube">
              <a
                href="https://github.com/alikhutaba"
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  className="github-img"
                  src="data:image/png;base64,"
                  alt="Click her"
                ></img>
              </a>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    action: state.timerReducer.timer,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    setTimer: (timer) => dispatch(setTimer(timer)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Timer);
